import React, {Fragment} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Divider from '@material-ui/core/Divider';
import {connect} from 'react-redux';
import {useLocation} from 'react-router-dom';
import {useDispatch} from 'react-redux';
import images from "./Images";

const useStyles = makeStyles((theme) => ({
  

    container: {
        display:"grid",
        gridTemplateColumns: '50% 50%',
        gridTemplateRows: '1',
        marginLeft: "auto",
        marginRight: "auto",
        width:"95%",
        maxWidth: "1024px",
        gap: "20px",
        
        [theme.breakpoints.down('sm')]: {
            gridTemplateColumns: '30% 70%',
        },
        [theme.breakpoints.down('xs')]: {
            gridTemplateColumns: '100%',
        },

    },

    container2: {
        display:"grid",
        gridTemplateColumns: '100%',
        gridTemplateRows: '1',
        marginLeft: "auto",
        marginRight: "auto",
        width:"95%",
        maxWidth: "1024px",
    },


    columnRight: {
        padding:"30px 30px 30px 30px",
        [theme.breakpoints.down('sm')]: {
            gridTemplateColumns: '100%',
            gridTemplateRows: '1 1',
            padding:"0px 15px 15px 15px",
            },
    },

    buttonContainer: {
        display:"flex",
        justifyContent:"flex-end",
        alignSelf:"end",
    },

    button: {
        maxHeight:"48px",
        minHeight:"48px",
        width:"20%",
        [theme.breakpoints.down('sm')]: {
            width:"30%",
            },
    },

    price: {
        fontSize:"18px",
        textAlign:"right",
        fontWeight:"600",
    },

    text: {
        paddingBottom:"16px",
    },

    textBold: {
        paddingBottom:"16px",
        fontWeight:"600",
    },

    headline: {
        padding:"0px 0px 16px 0px",
    },

    hint: {
        color: "#dd0000",
    },

    img: {
        display: 'block',
        maxWidth: '100%',
        maxHeight: '100%',
        minWidth: "160px",
        minHeight: "120px",
    },

    divider: {
        backgroundColor:"#bbbfcc",
        marginTop:"32px",
        marginBottom:"32px",
    },

}));

const Detail = (props) => {

    const classes = useStyles();
    let location = useLocation();
    const dispatch = useDispatch();
    let id = parseInt(location.pathname.split(":")[1]);

    let result = props.state.cartState.products.filter (function (obj) {
        if (obj.id === id) return obj;
        return false;
    });
    let data = result[0];

    
    let btn = <Button onClick={ () => dispatch({ type:"REMOVE_PRODUCT", id:data.id})} variant="contained" className={classes.button}>Entfernen</Button>;
  
    if (!data.inCart) {
        btn = <Button onClick={ () => dispatch({ type:"ADD_PRODUCT", id:data.id})} variant="contained" color="primary" className={classes.button}>Hinzufügen</Button>
    }
  
    
    return (

        <Fragment>
            <Divider className={classes.divider}/>

            <Grid container className={classes.container}>

                <Grid item>
                    <img className={classes.img} alt={data.name} src={ images[data.image]} />
                </Grid>

                <Grid item className={classes.columnRight}>
                    <Typography variant="h5" className={classes.headline} >{data.headline}</Typography>
                    <Typography variant="body1" className={classes.textBold} >{data.text1}</Typography>
                    <Typography variant="body1" className={classes.text} >{data.text2}</Typography>
                    <Typography variant="body1" className={classes.text} >{data.text3}</Typography>
                    <Typography variant="body1" className={classes.hint} >{data.hint}</Typography>
                    <Typography className={classes.price}>€ {data.price}</Typography>

                </Grid>
            </Grid>

            <Divider className={classes.divider}/>

            <Grid container className={classes.container2}>
           
                <Grid item className={classes.buttonContainer}>
                    {btn}
                </Grid>

            </Grid>


                
           
        </Fragment>
  );
};

const mapStateToProps = (state) => {

    return {state};
}

export default connect(mapStateToProps) (Detail);
