import React, {Fragment} from 'react';
import Header from './components/Header';
import Herobox from './components/Herobox';
import Headline from './components/Headline';
import CategoriesHeadline from './components/CategoriesHeadline';
import CardsMainHome from './components/CardsMainHome';
import CardsBestsellerHome from './components/CardsBestsellerHome';
import {Provider} from 'react-redux';
import Store from './Store';
import Cart from './components/Cart';
import Detail from './components/Detail';
import DetailHeadline from './components/DetailHeadline';
import CartHeadline from './components/CartHeadline';
import Unfug from './components/Unfug';
import Hygiene from './components/Hygiene';
import Gebrauchtes from './components/Gebrauchtes';
import Footer from './components/Footer'
import {Route, Switch} from 'react-router-dom';
import {HashRouter as BrowserRouter} from 'react-router-dom';



import './App.css';


const App = () => {

  return (
    <Provider store = {Store}>
        <BrowserRouter>
        <Header />
        <Herobox />
        <Switch>
        <Route exact path ="/" render={props =>
            <Fragment>
                <Headline text="Unser Sortiment"/>
                <CardsMainHome/>
                <Headline text="Bestseller"/>
                <CardsBestsellerHome/>
            </Fragment>
        }/>

        <Route path ="/unfug" render={props =>
            <Fragment>
                <CategoriesHeadline category="1"/>
                <Unfug/>
            </Fragment>
        }/>
        <Route path ="/hygiene" render={props =>
            <Fragment>
                <CategoriesHeadline category="2"/>
                <Hygiene/>
            </Fragment>
        }/>

        <Route path ="/gebrauchtes" render={props =>
            <Fragment>
                <CategoriesHeadline category="3"/>
                <Gebrauchtes/>
            </Fragment>
        }/>

        <Route path ="/cart" render={props =>
            <Fragment>
                <CartHeadline/>
                <Cart/>
            </Fragment>
        }/>

        <Route path ="/detail/:id" render={props =>
            <Fragment>
                <DetailHeadline/>
                <Detail/>
            </Fragment>
        }/>         
            
        </Switch>  
    
        <Footer/>

        </BrowserRouter>
    </Provider >
  );
};

export default App;
