export default (state = initialState, action) => {

    switch (action.type) {

        case GET_CATEGORY :
            // console.log("here in get category");

            // return state.products.map((item, index) => {
            //     console.log(item.group);

            //     if(item.group === 1) {
            //         return item;
            //     }
            // });
            return {...state}
        
        case ADD_PRODUCT :

            let newState = {...state};
            let sum = 0;

            newState.products = newState.products.map((item, index) => {
                
                if(item.id === action.id) {
                    sum += item.price;

                    // Return a new object
                    return {
                        ...item,  // copy existing item
                        inCart: true  // set new Value
                    }
                }
                if (item.inCart) sum += item.price;
                // Leave every other item unchanged
                return item;
                });
            newState.sum = parseFloat((Math.round(sum * 100) / 100).toPrecision(6));

            
    
            return newState;

            case REMOVE_PRODUCT :

                let newState2 = {...state};
                let sum2 = 0;
    
                newState2.products = newState2.products.map((item, index) => {
                    
                    if(item.id === action.id) {
    
                        // Return a new object
                        return {
                            ...item,  // copy existing item
                            inCart: false  // set new Value
                        }
                    }
                    if (item.inCart) sum2 += item.price;
                    // Leave every other item unchanged
                    return item;
                    });
                newState2.sum = parseFloat((Math.round(sum2 * 100) / 100).toPrecision(6));
        
                return newState2;

        case GET_NUMBERS_BASKET : 
            return {
                ...state
            }
            
        default:
           
            return {...state};

    }
}