import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {connect} from 'react-redux';
import CardInCart from './CardInCart';
import Button from '@material-ui/core/Button';
import Divider from '@material-ui/core/Divider';



const useStyles = makeStyles((theme) => ({
  
    container: {
        display: "block",
        marginLeft: "auto",
        marginRight: "auto",
        width:"95%",
        maxWidth: "1024px",
    },

    divider: {
        backgroundColor:"#000000",
        marginTop:"32px",
        marginBottom:"32px",
    },

    cartemptytext: {
        padding:"30px 0px 30px 0px",
        fontSize:"16px",
        textAlign:"center",
    },

    sumtext: {
        fontSize:"16px",
        textAlign:"right",
    },

    sum: {
        fontSize:"18px",
        textAlign:"right",
        fontWeight:"600",
        paddingTop:"12px",
        paddingBottom:"8px",
    },

    button: {
        display:"flex",
        maxHeight:"48px",
        minHeight:"48px",
        width:"200px",
        selfAlign:"end",
        marginLeft:"auto",
    },

  
}));

const Cart = (props) => {
    
    const classes = useStyles();
    let cartEmptyText;
    let okButton;

    if (props.sum === 0) {
        cartEmptyText = <div className={classes.cartemptytext}> Tja, Ihr Warenkorb ist völlig leer!</div>
    }
    else {
        okButton = <Button variant="contained" color="primary" className={classes.button}>Jetzt bestellen</Button>
    }

  
    return (

        <div className={classes.container}>

            {cartEmptyText}

            {
            props.products.map (function (obj) {
                return (<CardInCart key={obj.name} data={obj} ></CardInCart>); 
            })
            }

        <Divider className={classes.divider}/>
        <div className= {classes.sumtext}>Summe:</div>
        <div className= {classes.sum}>€ {props.sum}</div>
        <Divider className={classes.divider}/>
        
        {okButton}

        </div>
  );
};

const mapStateToProps = (state) => {
    let total = state.cartState.sum;
    let result = state.cartState.products.filter(function(p) {
        return (p.inCart === true) });
    return {products: result, sum:total};
}

export default connect(mapStateToProps) (Cart);

