import React from 'react'
import { makeStyles } from '@material-ui/core/styles';
import {Box, Typography} from "@material-ui/core";
import {connect} from 'react-redux';


const useStyles = makeStyles((theme) => ({

    container : {
        display:"flex",
        justifyContent: "flex-end",
        width:"95%",
        maxWidth: "1024px",
        marginLeft:"auto",
        marginRight:"auto",
        paddingTop:"20px",
    },
    text1: {
        fontSize: "16px",
        lineHeight:"18px",
        fontWeight:"400;",
        color: "rgb(120,130,140)",
        marginRight:"10px",
    },

    text2: {
        fontSize: "18px",
        lineHeight:"18px",
        fontWeight:"600;",
        color: "rgb(0,0,0)",
    }
  }));

const Sum = (props) => {

    const classes = useStyles();
    let sum = props.state.cartState.sum;

    return (

        <Box className = {classes.container}>
            <Typography className = {classes.text1}>Summe: </Typography>
            <Typography className = {classes.text2}> € {sum}</Typography>
        </Box>
    )
}

const mapStateToProps = (state) => {
    return {state};
}

export default connect(mapStateToProps) (Sum);
