import React, {Fragment} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Divider from '@material-ui/core/Divider';
import { Link } from 'react-router-dom';
import {useDispatch} from 'react-redux';
import images from "./Images";


const useStyles = makeStyles((theme) => ({
  
    container: {

        display: 'grid',
       
        [theme.breakpoints.up('xs')]: {
            gridTemplateColumns: '100%',
            gridTemplateRows: '1 1',
            gap: "10px",
            },
        [theme.breakpoints.up('sm')]: {
            gridTemplateColumns: '30% auto 25%',
            gridTemplateRows: 'auto',
            gap: "20px",
          },
    },
    
    headlineCategory: {

    },

    columnRight: {
        display: 'grid',
        gridTemplateColumns: 'auto',
        gridTemplateRows: '1f 1f',
    },

    buttonContainer: {
        display:"flex",
        justifyContent:"flex-end",
        alignSelf:"end",
    },

    button: {
        maxHeight:"48px",
        minHeight:"48px",
        width:"100%",
    },

    price: {
        fontSize:"18px",
        textAlign:"right",
        fontWeight:"600",
        color:"#12377c",
        paddingBottom:"12px",
    },

    text: {
        paddingBottom:"16px",
    },

    headline: {
        padding:"16px 0px 16px 0px",
    },

    hint: {
        color: "#dd0000",
        paddingBottom:"12px",
    },

    details: {
        display:"block",
        color: "#12377c",
        fontWeight:"500",
        paddingBottom:"12px",
    },

    img: {
        display: 'block',
        maxWidth: '100%',
        maxHeight: '100%',
        minWidth: "160px",
        minHeight: "120px",
    },

    divider: {
        backgroundColor:"#bbbfcc",
        marginTop:"32px",
        marginBottom:"32px",
    },

}));


const CardInList = (props) => {

    const classes = useStyles();
    const data = props.data;
    const dispatch = useDispatch();
    let pathToDetail = "/detail/:"+data.id;

    let btn = <Button onClick={ () => dispatch({ type:"REMOVE_PRODUCT", id:data.id})} variant="contained" className={classes.button}>Entfernen</Button>;
  
    if (!data.inCart) {
        btn = <Button onClick={ () => dispatch({ type:"ADD_PRODUCT", id:data.id})} variant="contained" color="primary" className={classes.button}>Hinzufügen</Button>
    }


    return (

        <Fragment>
        <Divider className={classes.divider}/>

        <Grid container className={classes.container}>

            <Grid item>
                <img className={classes.img} alt={data.name} src={ images[data.image]} />
            </Grid>

            <Grid item>
                <Typography variant="h5" className={classes.headline} >{data.headline}</Typography>
                <Typography variant="body1" className={classes.text} >{data.text1}</Typography>
                <Typography variant="body1" className={classes.text} >{data.text2}</Typography>
                <Typography variant="body1" className={classes.hint} >{data.hint}</Typography>

                <Link to={pathToDetail}>
                    <div className={classes.details}>Details</div>
                </Link>

            </Grid>

            <Grid item container className={classes.columnRight}> 
                <Typography className={classes.price}>€ {data.price}</Typography>
                <Grid item className={classes.buttonContainer}>
                    {btn}
                </Grid>
            </Grid>
            
        </Grid>
        </Fragment>
    );
}


export default CardInList;

