
import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {connect} from 'react-redux';
import CardInList from './CardInList';


const useStyles = makeStyles((theme) => ({
  
    container: {
        display: "block",
        marginLeft: "auto",
        marginRight: "auto",
        width:"95%",
        maxWidth: "1024px",
    },
  
}));

const Gebrauchtes = (props) => {
    
    const classes = useStyles();
  
    return (
   
        <div className={classes.container}>
            {
            props.products.map (function (obj) {
                return (<CardInList key={obj.name} data={obj} ></CardInList>); 
            })
            }
        </div>
  );
};

const mapStateToProps = (state) => {
    let result = state.cartState.products.filter(function(p) {
        return (p.group === 3) });
    return {products: result};
}

export default connect(mapStateToProps) (Gebrauchtes);

